.my-head {
  position: absolute;
  width: 230px;
  height: 70px;
  background-color: rgba(155, 155, 155, 0.5);
  top: 5%;
  left: 2%;
  border-radius: 15px;
  z-index: 3;
  cursor: pointer;
}
.my-head img {
  position: relative;
  width: 50px;
  height: 50px;
  border-radius: 50%;
  margin: 10px;
}
.my-head h5 {
  position: absolute;
  top: 12px;
  left: 70px;
}
.my-head h6 {
  position: absolute;
  top: 42px;
  left: 80px;
  color: black;
}
.my-head span {
  padding: 0 8px;
  border-radius: 5px;
  background-color: black;
  color: white;
}
.my-head:hover {
  color: #964300;
}
